open StringMap

type __ = Obj.t

type 'v coq_OM = 'v M.t

(** val empty : 'a1 coq_OM **)

let empty = M.empty

(** val add : string -> 'a1 -> 'a1 coq_OM -> 'a1 coq_OM **)

let add = M.insert

(** val lookup : string -> 'a1 coq_OM -> 'a1 option **)

let lookup = M.lookup

(** val size : 'a1 coq_OM -> int **)

let size = M.size

(** val coq_OMap : __ coq_OM coq_StringMap **)

let coq_OMap =
  { StringMap.empty = (fun _ -> empty); StringMap.add = (fun _ -> add);
    StringMap.lookup = (fun _ -> lookup); StringMap.size = (fun _ -> size) }
