open BearGame
open Game
open Graph
open OCamlTB
open OMap
open Player
open RomanWheel
open Show
open Sort
open Strategy

(** val make_BG_State :
    coq_Graph -> coq_Player -> coq_Vert -> coq_Vert -> coq_Vert -> coq_Vert
    -> coq_BG_State option **)

let make_BG_State g pl b h1 h2 h3 =
  let s =
    coq_NoDup_dec (coq_Graph_Vert_disc g) (b :: (h1 :: (h2 :: (h3 :: []))))
  in
  if s
  then Some { BearGame.to_play = pl; bear = b; hunters =
         (insertion_sort
           (subcount_Ord (coq_Graph_Vert_disc g) (coq_Graph_Vert_enum g))
           (h1 :: (h2 :: (h3 :: [])))) }
  else None

(** val make_RW_State :
    coq_Player -> coq_RWVert -> coq_RWVert -> coq_RWVert -> coq_RWVert ->
    coq_BG_State option **)

let make_RW_State pl b h1 h2 h3 =
  make_BG_State coq_RomanWheel pl (Obj.magic b) (Obj.magic h1) (Obj.magic h2)
    (Obj.magic h3)

(** val init_RW_State : coq_BG_State **)

let init_RW_State =
  { BearGame.to_play = White; bear = (Obj.magic Center); hunters =
    ((Obj.magic (SpokeVert (S1, Mid))) :: ((Obj.magic (SpokeVert (S1, L))) :: (
    (Obj.magic (SpokeVert (S1, R))) :: []))) }

(** val print_RW_move : coq_BG_State -> coq_BG_Move -> string **)

let print_RW_move _ = function
| BearMove b -> (^) "B" (show coq_Show_RWV b)
| HunterMove h ->
  let { h_orig = h_orig0; h_dest = h_dest0 } = h in
  (^) "H" ((^) (show coq_Show_RWV h_orig0) (show coq_Show_RWV h_dest0))

(** val coq_RW_exec_move : coq_BG_State -> coq_BG_Move -> coq_BG_State **)

let coq_RW_exec_move s =
  BearGame.exec_move coq_RomanWheel s

(** val show_RW_State : coq_BG_State -> string **)

let show_RW_State s =
  show (Obj.magic coq_BearGameStateShow coq_RomanWheel coq_Show_RWVert) s

(** val query_RW_TB :
    coq_OCamlTablebase -> coq_BG_State -> (coq_Player * int) option **)

let query_RW_TB =
  Obj.magic query_TB (coq_BearGame coq_RomanWheel)
    (coq_BearGameStateShow coq_RomanWheel (Obj.magic coq_Show_RWVert))

(** val rw_tb_strat :
    coq_Player -> coq_GameState -> coq_OCamlTablebase -> strategy **)

let rw_tb_strat pl s tb =
  tb_strat (coq_BearGame coq_RomanWheel) s pl coq_OMap
    (coq_BearGameStateShow coq_RomanWheel (Obj.magic coq_Show_RWVert)) tb
