open List0

type 'x coq_Show =
  'x -> string
  (* singleton inductive, whose constructor was Build_Show *)

(** val show : 'a1 coq_Show -> 'a1 -> string **)

let show show0 =
  show0

(** val coq_Show_dec : 'a1 coq_Show -> 'a1 -> 'a1 -> bool **)

let coq_Show_dec h x x' =
  (=) (show h x) (show h x')

(** val coq_Show_list : 'a1 coq_Show -> 'a1 list coq_Show **)

let coq_Show_list sh xs =
  (^) "[" ((^) (String.concat ";" (map (show sh) xs)) "]")
