open Dec
open Show

type __ = Obj.t
let __ = let rec f _ = Obj.repr f in Obj.repr f

type 'm coq_StringMap = { empty : (__ -> 'm);
                          add : (__ -> string -> __ -> 'm -> 'm);
                          lookup : (__ -> string -> 'm -> __ option);
                          size : (__ -> 'm -> int) }

(** val lookup : 'a1 coq_StringMap -> string -> 'a1 -> 'a2 option **)

let lookup stringMap x x0 =
  Obj.magic stringMap.lookup __ x x0

(** val str_lookup :
    'a1 coq_StringMap -> 'a2 coq_Show -> 'a2 -> 'a1 -> 'a3 option **)

let str_lookup h h0 x =
  lookup h (show h0 x)

(** val in_dec : 'a1 coq_Discrete -> 'a1 -> 'a1 list -> bool **)

let rec in_dec h x = function
| [] -> false
| y :: l -> let d = eq_dec h y x in if d then true else in_dec h x l

(** val in_decb : 'a1 coq_Discrete -> 'a1 -> 'a1 list -> bool **)

let in_decb h x xs =
  if in_dec h x xs then true else false

(** val coq_Show_disc : 'a1 coq_Show -> 'a1 coq_Discrete **)

let coq_Show_disc =
  coq_Show_dec
