open Graph
open List0
open Show
open StringMap

type coq_Spoke =
| S1
| S2
| S3
| S4
| S5
| S6
| S7
| S8

(** val clockwise : coq_Spoke -> coq_Spoke **)

let clockwise = function
| S1 -> S2
| S2 -> S3
| S3 -> S4
| S4 -> S5
| S5 -> S6
| S6 -> S7
| S7 -> S8
| S8 -> S1

(** val c_clockwise : coq_Spoke -> coq_Spoke **)

let c_clockwise = function
| S1 -> S8
| S2 -> S1
| S3 -> S2
| S4 -> S3
| S5 -> S4
| S6 -> S5
| S7 -> S6
| S8 -> S7

(** val list_spokes : coq_Spoke list **)

let list_spokes =
  S1 :: (S2 :: (S3 :: (S4 :: (S5 :: (S6 :: (S7 :: (S8 :: [])))))))

(** val show_spoke : coq_Spoke -> string **)

let show_spoke = function
| S1 -> "1"
| S2 -> "2"
| S3 -> "3"
| S4 -> "4"
| S5 -> "5"
| S6 -> "6"
| S7 -> "7"
| S8 -> "8"

type coq_SpokeLoc =
| Mid
| L
| R

(** val show_loc : coq_SpokeLoc -> string **)

let show_loc = function
| Mid -> "m"
| L -> "l"
| R -> "r"

(** val list_locs : coq_SpokeLoc list **)

let list_locs =
  Mid :: (L :: (R :: []))

type coq_RWVert =
| Center
| SpokeVert of coq_Spoke * coq_SpokeLoc

(** val coq_Show_RWVert : coq_RWVert coq_Show **)

let coq_Show_RWVert = function
| Center -> "c"
| SpokeVert (s, l) -> (^) (show_loc l) (show_spoke s)

(** val coq_RomanWheel : coq_Graph **)

let coq_RomanWheel =
  { coq_Vert_disc = (coq_Show_disc (Obj.magic coq_Show_RWVert));
    coq_Vert_enum =
    ((Obj.magic Center) :: (concat
                             (map (fun s ->
                               map (Obj.magic (fun x -> SpokeVert (s, x)))
                                 list_locs) list_spokes))); successors =
    (fun v ->
    match Obj.magic v with
    | Center -> map (fun s -> Obj.magic (SpokeVert (s, Mid))) list_spokes
    | SpokeVert (s, l) ->
      (match l with
       | Mid ->
         (Obj.magic Center) :: ((Obj.magic (SpokeVert (s, L))) :: ((Obj.magic
                                                                    (SpokeVert
                                                                    (s, R))) :: []))
       | L ->
         (Obj.magic (SpokeVert (s, R))) :: ((Obj.magic (SpokeVert (s, Mid))) :: (
           (Obj.magic (SpokeVert ((c_clockwise s), R))) :: []))
       | R ->
         (Obj.magic (SpokeVert (s, L))) :: ((Obj.magic (SpokeVert (s, Mid))) :: (
           (Obj.magic (SpokeVert ((clockwise s), L))) :: [])))) }

(** val coq_Show_RWV : coq_Vert coq_Show **)

let coq_Show_RWV =
  Obj.magic coq_Show_RWVert
